:root {
  /*Colors*/
  --green: #74fd93;
  --red: #f16c6c;
  --purple: #9100ff;
  --white: #F5F5F5;
  --gradient: linear-gradient(45deg, #f16c6c, #9100ff);

  /*Typography*/
  --font: 'Major Mono Display', monospace;
}

* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

html,
body {
  padding: 0;
  margin: 0;
  font-size: 10px;
}

body {
  font-size: 2rem;
  font-family: var(--font);
}

.App {
  text-align: center;
  height: 100vh;
  -webkit-transition: 1s;
  -o-transition: 1s;
  transition: 1s;
  -webkit-animation-duration: 2s;
          animation-duration: 2s;
  -webkit-animation-timing-function: ease-out;
          animation-timing-function: ease-out;
}

.App--pass {
  -webkit-animation-name: pass;
          animation-name: pass;
}

.App--fail {
  -webkit-animation-name: fail;
          animation-name: fail;
}

button {
  background: var(--gradient);
  font-family: var(--font);
  font-size: 3rem;
  border: none;
  padding: 2rem;
  -ms-flex-preferred-size: 10rem;
      flex-basis: 10rem;
  height: 10rem;
  color: var(--white);
}

button[disabled] {
  opacity: 0.8;
  background: -webkit-linear-gradient(135deg, #c16c6c, #5100ff);
  background: -o-linear-gradient(135deg, #c16c6c, #5100ff);
  background: linear-gradient(-45deg, #c16c6c, #5100ff);
}

h1 {
  font-size: 2em;
}

p {
  font-size: 1em;
}

/* The animation code */
@-webkit-keyframes pass {
  0% {
    background-color: var(--green);
  }

  100% {
    background-color: white;
  }
}
@keyframes pass {
  0% {
    background-color: var(--green);
  }

  100% {
    background-color: white;
  }
}

@-webkit-keyframes fail {
  0% {
    background-color: var(--red)
  }

  100%% {
    background-color: white
  }
}

@keyframes fail {
  0% {
    background-color: var(--red)
  }

  100%% {
    background-color: white
  }
}


